import {
	Box,
	Checkbox,
	List,
	ListItem,
	ListItemAvatar,
	ListItemText,
} from '@material-ui/core';
import Icon from '@material-ui/core/Icon'
import { useCallback, useEffect, useState } from 'react';
import { Category } from '../../../models';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Collapse from '@material-ui/core/Collapse';

interface Props {
	items: Category[];
	onChange(values: Category[]): any;
	categoriesList: Cat[];
}
interface Cat {
	name?: string | undefined;
	icon?: string | undefined;
	items?: string[];
}
export function CategoryPicker(props: Props) {

	const [Categories, setCategories] = useState<Cat[]>([]);

	useEffect(()=>{
			setCategories(props.categoriesList);
	},[])
	const onChecked = useCallback(
		(category: Category, check: boolean) => {
			let next = [...props.items];
			if (check) {
				if (!next.includes(category)) {
					next.push(category);
				}
			} else {
				next = next.filter(c => c !== category);
			}
			props.onChange(next);
		},
		[props]
	);

	const onCheckedMain = useCallback(
		(category: string[], check: boolean, mainCat:Cat) => {
			let next = [...props.items];

				if(category.length === 0) {
					if (check) {
						if (!next.includes(mainCat.name as Category)) {
							next.push(mainCat.name as Category);
						}
					} else {
						next = next.filter(c => c !== mainCat.name as Category);
					}
					
				} else {

				
				category.forEach(s=> {
					if (check) {
					if (!next.includes(s as Category)) {
						next.push(s as Category);
					}
				} else {
					next = next.filter(c => c !== s as Category);
				}
				})
			}
			
			props.onChange(next);
		},
		[props]
	);
	const [open, setOpen] = useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

	return (
		<Box width='55vw'>
			<List dense disablePadding>
				{Categories.length > 0 && Categories.map(c => {
					let mainchecked = true;
					// @ts-ignore: Object is possibly 'null'.
					if(c.items.length === 0) {
						if(!props.items.includes(c.name as Category)) {
							mainchecked= false;
						}
					} else {
						// @ts-ignore: Object is possibly 'null'.
						c.items.forEach(s=> {
							if(!props.items.includes(s as Category)) {
								mainchecked= false;
							}
						})
					}
					
					return (
						<div style={{display: 'flex', flexDirection:'column'}}>
						<ListItem dense button key={c.name} onClick={handleClick}>
							<ListItemAvatar>
								<div style={{display: 'flex'}}>
								
								<Checkbox
									color='primary'
									size='small'
									checked={mainchecked}
									onChange={() => onCheckedMain(c.items?? [], !mainchecked, c)}
								/>
								<Icon color='primary' style={{margin: '7px', fill: "green"}}>{c.icon}</Icon>
								  
								</div>
							</ListItemAvatar>
							<ListItemText primary={c.name} />
							{/* {c.name != "vlogging" ? ({open ? (<ExpandLess/>) : (<ExpandMore />)}):("")} */}
							{open ? <ExpandLess /> : <ExpandMore />}
							</ListItem>
							<div>
								
							<List dense disablePadding>
								<Collapse in={open} timeout="auto" unmountOnExit style={{marginLeft:'65px'}}>
							 {/* @ts-ignore: Object is possibly 'null'. */}
							{c.items.map(item => {
								let checked = props.items.includes(item as Category);
					return (
						<div >
							
						<ListItem dense button key={item}>
							<ListItemAvatar>
								<div >
								
								<Checkbox
									color='primary'
									size='small'
									checked={checked}
									onChange={() => onChecked(item  as Category, !checked)}
								/>
								  
								</div>
							</ListItemAvatar>
							<ListItemText primary={item} />
						</ListItem>
						
						</div>
						
						
					);
				})}
				</Collapse>
				</List>
				</div>
						{/* </ListItem> */}
						</div>
						
					);
				})}
			</List>
		</Box>
	);
}
