import { Box } from '@material-ui/core';
import { ChangeEvent, useCallback, useEffect, useMemo, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import { Loader } from '../ui/Loader';
import { OTPInput } from '../ui/OTPInput';
import { SingleLineInput } from '../ui/SingleLineInput';
import { userFromUname } from '../../core/utils';
import { Validators } from '../../core/validators';
import { __tr } from '../../i18n';
import { Routes } from '../../routes';
import styles from '../../styles/ConfirmOtp.module.scss';
import axios from 'axios';

export function ResetPassword(props: {user:any}) {
  const [otp, setOTP] = useState('');
  const [errors, setErrors] = useState<any>({});
  const [loading, setLoading] = useState(false);

  const history = useHistory();

  const onResendOtp = useCallback(() => {
    if (loading) {
      return;
    }

    setLoading(true);
  }, [loading]);

  const signIn = (password:string) => {
    const postBody = {
      password,
      email: props.user.email,
      phone: props.user.phone
    }
    axios.post('/api/auth/login',postBody).then(res=>{
      localStorage.setItem('token',res.data.token);
      return history.replace(Routes.Home);
    }).catch((err) => {
          toast.error(err.message);
        });
  }

  const onCompleted = useCallback(
    (ev: ChangeEvent<HTMLFormElement>) => {
      ev.preventDefault();
      if (loading) {
        return;
      }

      if (!otp) {
        toast.warn(__tr('pleaseEnterOtp'));
        return;
      }

      if (otp) {
        let form = ev.currentTarget;

        let newErrors: any = {};
        let password = form.newPassword.value;
        let confirm = form.confirmPassword.value;

        if (!Validators.isValidPassword(password)) {
          newErrors.newPassword = __tr('errorInvalidPassword');
        } else {
          if (confirm !== password) {
            newErrors.confirmPassword = __tr('errorPasswordDontMatch');
          }
        }

        setErrors(newErrors);

        if (Object.keys(newErrors).length > 0) {
          return;
        }

        setLoading(true);

        if(props.user.phone) {
          const mob = props.user.phone.split(" ");
          if(mob.length === 2) {
          axios.post('/api/auth/password/reset',{isPhone:true,countryCode:mob[0],number:mob[1],otp,userId:props.user._id,password})
          .then(res=> {
            console.log(res);
            setLoading(false);
              /// account verification is now complete.
              toast.success("Password Reset Successfully");
              signIn(password);
              //history.replace(Routes.Home);
            
          }).catch( err=>{
            console.log('eeeeeeeeeee',err)
          })
          
          ;}
          else {
            toast.error("Incorrect Mobile Number");
          }
        } else {
          axios.post('/api/auth/password/reset',{isPhone:false,otp,userId:props.user._id,password}).then(res=> {
              
          setLoading(false);
              /// account verification is now complete.
              toast.success("Password Reset Successfully");
              signIn(password);
              //history.replace(Routes.Home);
            
          }).catch( err=>{
            console.log('eeeeeeeeeee',err)
          });
        }

        // user?.confirmPassword(otp, password, {
        //   onSuccess() {
        //     setLoading(false);
        //     toast.success(__tr('passwordReset'));
        //     history.replace(Routes.Login);
        //   },
        //   onFailure(err) {
        //     toast.error(__tr('failurePasswordReset'));
        //   },
        // });
      }
    },
    [loading, history, props.user, otp]
  );

  return (
    <div className={styles.loginWrapper}>
      <div className={styles.formWrapper}>
        <h3>{__tr('btnResetPassword')}</h3>
        <p className={styles.subtitle}>{__tr('pleaseEnterOtp')}</p>
        <form action='' className={styles.form} onSubmit={onCompleted}>
          {loading && <Loader />}
          <Box marginY={2} marginTop={6}>
            <OTPInput length={6} onChange={setOTP} />
          </Box>
          <Box marginY={2} display='flex' flexDirection='column' alignItems='center' justifyContent='center'>
            <button className={styles.resendBtn} onClick={onResendOtp}>
              {__tr('resendOtp')}
            </button>
          </Box>
          {otp && (
            <>
              <Box marginY={2}>
                <SingleLineInput
                  label={__tr('labelCreatePassword')}
                  name='newPassword'
                  placeholder='john@example.com'
                  type='password'
                  error={errors.newPassword}
                />
              </Box>
              <Box marginY={2}>
                <SingleLineInput
                  label={__tr('labelConfirmPassword')}
                  name='confirmPassword'
                  placeholder={__tr('placeholderSignupPassword')}
                  type='password'
                  error={errors.confirmPassword}
                />
              </Box>
              <Box marginY={4}>
                <button type='submit' className={styles.btnSubmit}>
                  {__tr('btnSubmit')}
                </button>
              </Box>
            </>
          )}
        </form>
      </div>
    </div>
  );
}
