import React from 'react';
import styles from './messageSent.module.scss';
import CheckOutlinedIcon from '@material-ui/icons/CheckOutlined';

interface props {
  key: string;
  text: string;
  time: string;
  image?: string;
}

const MessageSent: React.FC<props> = ({ key, image, text, time }) => {
  return (
    <div className={styles.Container} key={key}>
      {image && <img src={image} alt='message' className={styles.TextImage} />}
      <p className={styles.Text}>{text}</p>
      <div style={{display:"flex",flexDirection:"row" ,justifyContent:"flex-end"}}>
      <p className={styles.Time}>{time}</p>
      <CheckOutlinedIcon style={{fontSize:"0.8rem" ,marginTop:"1%",marginLeft:"3%"}} size="small"></CheckOutlinedIcon>
      </div>
     
      
    </div>
    
  );
};

export default MessageSent;
