import {
  InputBase,
  Box,
  Grid,
  Typography,
  IconButton,
  Popover,
  MenuItem,
} from "@material-ui/core";
import { IconSearch } from "../ui/Icons";
import { NavLink } from "react-router-dom";
import styles from "../../styles/TopAppBar.module.scss";
import { MoreVert } from "@material-ui/icons";
import { useEffect, useState } from "react";
import { Routes } from "../../routes";
import Reject from "./Reject";
import Approve from "./Approve";
import axios from "axios";
import Brandimage from "../../assets/images/BrandImage.JPG";
import { useDispatch } from "react-redux";
import { usePostedCampaigns } from "../../state/selectors";
interface ids {
  bid: any;
  cid: any;
}

export function SuperAdmin() {
  const [openOptions, setOpenOptions] = useState<boolean>(false);
  const [selectedItem, setSelectedItem] = useState<any>();
  const [menuAnchorEl, setMenuAnchorEl] = useState<HTMLElement>();
  const [search, setSearch] = useState();
  const [data, setdata] = useState([]);
 

  const handleOptionsModalOpen = (el: HTMLElement,item:any) => {
    setMenuAnchorEl(el);
    setOpenOptions(true);
    setSelectedItem(item)
  };

  //open options modal
  const handleOptionsModalClose = () => {
    setMenuAnchorEl(undefined);
    setOpenOptions(false);
  };

  const token = localStorage.getItem("token");
  // console.log(token);
  useEffect(() => {
    const searchData = { campaignName: search};

    axios
      .post(`/api/campaign/searchCampaign`, searchData, {
        headers: { Authorization: token },
      })
      .then((res) => setdata(res.data));
  }, [data, search]);

  return (
    <>
      <Box
        display="flex"
        flexDirection="column"
        justifyContent="center"
        alignItems="center"
        marginTop="2em"
      >
        <Box>
          <InputBase
            placeholder={"Search Campaign"}
            startAdornment={<IconSearch size={18} />}
            className={styles.searchInput}
            onChange={(e: any) => setSearch(e.target.value)}
          />
        </Box>
        {data?.map((item, index) => {
          return (
            <>
              <Grid container>
                <Grid item xs={6}>
                  <Box paddingX={35} paddingY={5}>
                    {/* <NavLink
              to={Routes.	CreateCampaign}
              // className={styles.navigationItem}
              // activeClassName={styles.navigationItemActive}
            >
              <span >{item.name}</span>
            </NavLink> */}
                    <div style={{ display: "flex", width: "12.5rem" }}>
                      <div
                        className="col-lg-1 col-3 f-icon b-img feature-p-0"
                        style={{
                          display: "flex",
                          marginRight: "40px",
                        }}
                      >
                        {item.brand?.brandImageUrl ? (
                          <img
                            src={item?.brand?.brandImageUrl}
                            style={{ objectFit: "contain" }}
                            alt={item?.brand?.name}
                          />
                        ) : (
                          <img
                            src={Brandimage}
                            style={{ objectFit: "contain" }}
                            alt={item?.brand?.name}
                          />
                        )}
                      </div>
                      <div>
                        <div>
                          <Typography style={{ fontWeight: "bold" }}>
                            {item?.Name}
                          </Typography>
                        </div>

                        <div>
                          <Typography>
                            {item?.createdAt.slice(0, 10)}
                          </Typography>
                        </div>

                        {item?.CampaignStatus === "SUBMITTED" ||
                        item?.CampaignStatus === "DRAFT" ? (
                          <div>
                            <Typography style={{ color: "#EEBA00" }}>
                              {item?.CampaignStatus}
                            </Typography>
                          </div>
                        ) : item?.CampaignStatus === "REJECTED" ? (
                          <Typography style={{ color: "red" }}>
                            {item?.CampaignStatus}
                          </Typography>
                        ) : (
                          <div>
                            <Typography style={{ color: "green" }}>
                              {item?.CampaignStatus}
                            </Typography>
                          </div>
                        )}
                      </div>
                    </div>
                  </Box>
                </Grid>
                <Grid item xs={4}>
                  <Box paddingX={2} paddingY={5} display="flex">
                    {/* <Button variant='contained' style={{backgroundColor:"green", color:"white",marginRight:"20px"}}>Approve</Button>
                   <Button variant='contained'color="secondary">Reject</Button> */}
                    <Approve bid={item?.brand?._id} cid={item?._id} />

                    <Reject bid={item?.brand?._id} cid={item?._id} />
                  </Box>
                </Grid>
                <Grid item xs={2}>
                  <Box margin="-20px" className={styles.actions}>
                    <IconButton
                      onClick={(ev: React.MouseEvent<HTMLElement>) => {
                        handleOptionsModalOpen(ev.currentTarget,item);
                      }}
                    >
                      <MoreVert fontSize="medium" />
                    </IconButton>
                  </Box>
                  {selectedItem && 
                  <Popover
                    open={openOptions}
                    onClose={handleOptionsModalClose}
                    anchorEl={menuAnchorEl}
                    anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
                    elevation={2}
                  >
                    <Box minWidth="150px">
                      <MenuItem>
                        <NavLink
                          to={Routes.EditCampaign(selectedItem?._id || "")}
                        >
                          <span>edit</span>
                        </NavLink>
                      </MenuItem>
                      <MenuItem>
                        <NavLink
                          to={Routes.viewCampaignApplicants(selectedItem?._id || "")}
                          >
                          <span>View</span>
                        </NavLink>
                      </MenuItem>
                    </Box>
                  </Popover>}
                </Grid>
              </Grid>
            </>
          );
        })}
      </Box>
    </>
  );
}
