import * as React from "react";
import { ChangeEvent, useEffect, useState } from "react";
import { Button, Dialog, DialogActions, TextField } from "@material-ui/core";
import axios, { AxiosResponse } from "axios";
import { AnyIfEmpty } from "react-redux";
import { Search } from "@material-ui/icons";
import { AnyRecord } from "dns";

const defaultState = {
  title: "",
  image: "",
};
interface IProps {
  bid: any;
  cid: any;
}

export default function Approve(props: IProps) {
  const [open, setOpen] = useState(false);
  const [image, setimage] = useState<any>({ selectedFile: "" });
  const [imageUrl, setimageUrl] = useState();
  const [title, settitle] = useState();

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handlerInputChange = (event: any) => {
    let files = event.target.files;
    let reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = (e: any) => {
      setimage({ selectedFile: e.target?.result });
    };
  };
  const token = localStorage.getItem("token");
  const submit = () => {
    const formData = { image: image.selectedFile, name: "name" };

    if (token) {
      axios
        .post(`https://wobb-ui.herokuapp.com/api/s3/upload`, formData, {
          headers: { Authorization: token },
        })
        .then((res) => {
          setimageUrl(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });
    }
  };

  const updateCampaginName = (id: any) => {
    const updateCampaginName = { Name: title };
    if (token) {
      axios
        .put(`/api/campaign/${id}`, updateCampaginName, {
          headers: { Authorization: token },
        })
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });
    }
  };

  const updateBrandlogo = (id: any) => {
    const updatelogo = { brandImageUrl: imageUrl };
    if (token) {
      axios
        .put(`/api/brand/${id}`, updatelogo, {
          headers: { Authorization: token },
        })
        .then((res) => {
          // console.log(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });
    }
  };

  // updatedData(61f8c695854008001907e255,);
  const updateCampaignName = (e: ChangeEvent<HTMLInputElement>) => {
    settitle(e.target.value);
  };

  const updateCampaginStatusApproved = () => {
    const updateCampaginStatus = {
      status: "APPROVED",
      campaignId: props.cid,
    };
    if (token) {
      axios
        .post(
          `/api/campaign/changeStatus/`,
          updateCampaginStatus,
          {
            headers: { Authorization: token },
          }
        )
        .then((res) => {
          console.log(res.data);
        })
        .catch((err) => {
          console.log("err", err.response.data);
        });
    }
  };

  const approve = () => {
    updateCampaginName(props.cid);
    updateBrandlogo(props.bid);
    updateCampaginStatusApproved();

    handleClose();
  };

  return (
    <div style={{ height: "50%", width: "50%" }}>
      <Button
        variant="contained"
        onClick={handleClickOpen}
        style={{
          backgroundColor: "green",
          color: "white",
          marginRight: "20px",
        }}
      >
        Approve
      </Button>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <div style={{ padding: "20px" }}>
          <input
            type="file"
            name="select-image"
            id="select-image"
            onChange={handlerInputChange}
            // style={{ display: "none" }}
          />

          <label>
            <Button variant="contained" color="primary" onClick={submit}>
              Upload Image
            </Button>
          </label>
          {imageUrl ? (
            <p style={{ color: "green" }}>Successful</p>
          ) : (
            <p style={{ color: "red" }}> Please Upload a Image</p>
          )}

          <TextField
            size="small"
            variant="outlined"
            fullWidth
            placeholder="Title"
            id="title"
            name="title"
            value={title}
            onChange={updateCampaignName}
            style={{ marginTop: "10px" }}
          />
          <DialogActions>
            <Button
              onClick={approve}
              style={{
                backgroundColor: "green",
                color: "white",
                marginRight: "20px",
              }}
            >
              Approved
            </Button>
          </DialogActions>
        </div>
      </Dialog>
    </div>
  );
}
